import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { listProducts } from '../actions/Product';
import Rating from '../components/Rating';
import { ProductType } from '../types/ProductType';

type RouteInfo = {
    id: string;
};

export default function ProductPage({ match }: RouteComponentProps<RouteInfo>) {

    const dispatch = useDispatch();
    const proudctList = useSelector((state: any) => state.proudctList);
    const { products } = proudctList;
    const [product, setProduct] = useState<ProductType>();

    useEffect(() => {
        dispatch(listProducts());
    }, [dispatch]);

    useEffect(() => {
        const tempProduct = products?.find((x: any) => x._id === match.params.id);
        setProduct(tempProduct);
    }, [products, match.params.id]);


    if (!product) {
        return <div>Product Not Found</div>
    }

    return (
        <div>
            <Link to="/">Back to Result</Link>
            <div className="row top">
                <div className="col-2">
                    <img className="large" src={process.env.PUBLIC_URL + product?.image} alt="" />
                </div>
                <div className="col-1">
                    <ul>
                        <li>
                            <h1>{product?.name}</h1>
                        </li>
                        <li>
                            <Rating rating={product.rating} numReviews={product.numReviews} />
                        </li>
                        <li>
                            Price : {product.price}
                        </li>
                        <li>
                            Description
                            <p>{product.description}</p>
                        </li>
                    </ul>
                </div>
                <div className="col-1">
                    <div className="card card-body">
                        <ul>
                            <li>
                                <div className="row">
                                    <div>Price</div>
                                    <div className="price">
                                        ${product.price}
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="row">
                                    <div>Status</div>
                                    <div >
                                        {product.countInStock > 0 ?
                                            <span className="success">
                                                In Stock
                                        </span> :
                                            <span className="Error">
                                                Unavailable
                                            </span>
                                        }

                                    </div>
                                </div>
                            </li>
                            <li>
                                <button className="primary">Add To Cart</button>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}
